/* --- NAVIGATION BAR --- */

.navigation-bar {
    height: 60px;
    width: 100%;

    display: flex;
    align-items: center;

    position: relative;
    padding: 0;
    background-color: inherit;
    color: black;
}

.navigation-logo {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: auto;
}

.navigation-menu {
    margin-left: auto;
    margin-right: auto;
}

.navigation-menu ul {
    display: flex;
    padding: 0;
    margin: 0;
}

.navigation-menu li {
    list-style-type: none;
    margin: 0 1rem;
}

.navigation-menu li a {
    color: rgb(123, 123, 123);
    text-decoration: none;
    display: block;
    width: 100%;
}

.navigation-menu li a:hover {
    color: rgb(193, 72, 67);
    font-weight: bold;
}

.hamburger {
    border: 0;
    height: 50px;
    width: 50px;
    padding: 0.5rem;
    border-radius: 0%;
    background-color: black;
    filter: opacity(80%);

    cursor: pointer;
    transition: background-color 0.1s ease-in-out;

    position: absolute;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);

    display: none;
}

.hamburger:hover {
    background-color: rgb(193, 72, 67);
}



/* --- LOGO IMAGE --- */

.logo-image img {
    display: block;
    width: 100%;
    height: calc(20vh + 20vw);
    object-fit: cover;
    object-position: 50% 27.5%;
}



/* --- SOCIAL MEDIA ICONS --- */

header .social-media-links ul {
    display: flex;

    position: absolute;
    right: 5px;
    top: calc(20vh + 20vw + 15px);

    padding: 0;
    margin: 0;
}

header .social-media-links li {
    list-style-type: none;
    margin: 0.5rem;
}

header .social-media-links img {
    width: 25px;
    height: 25px;
    padding: 10%;
    border-radius: 50%;
    filter: opacity(30%);
}

header .social-media-links img:hover {
    filter: opacity(100%);
}



/* --- MOBILE VIEW --- */

@media screen and (max-width: 730px) {
    .navigation-menu.expanded ul {
        display: block;
    }

    header .social-media-links.expanded {
        display: none;
    }

    .navigation-menu ul {
        display: none;

        position: absolute;
        top: 60px;
        left: 0;
        flex-direction: column;
        width: 100%;
        height: calc(100vh - 77px);
        background-color: white;
        border-top: 1px solid black;
    }

    .navigation-menu li {
        text-align: center;
        margin: 0;
    }

    .navigation-menu li a {
        color: black;
        width: 100%;
        padding: 1.5rem 0;
    }
    
    .navigation-menu li a:hover {
        background-color: rgb(191, 191, 191);
        font-weight: bold;
    }

    .hamburger {
        display: block;
    }

    header .social-media-links ul {
        flex-direction: column;
        top: 60px;
    }
    
    header .social-media-links img {
        width: calc(50px - 0.5rem);
        height: calc(50px - 0.5rem);
        filter: opacity(70%);
    }
}