/* --- SOCIAL MEDIA ICONS --- */

footer .social-media-links ul {
    display: flex;
    justify-content: center;

    padding: 0;
    margin: 0;
}

footer .social-media-links li {
    list-style-type: none;
    margin: 0 1.0rem;
}

footer .social-media-links img {
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 10%;
    border-radius: 50%;
    filter: invert(100%);
}

.social-media-links img:hover {
    filter: invert(0%);
}



/* --- TEXT --- */

.factotum-red {
    color: rgb(193, 72, 67);
}

.text {
    color: inherit;
    font-size: medium;
    line-height: 1.75em;
}